@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
  text-align: center;
  color: #424874;
}
.container {
  padding: 1rem;
  background-color: #f4eeff;
  min-height: 100vh;
}
.main-title {
  text-decoration: underline;
  font-weight: 500;
  font-size: 3rem;
}
.search-area {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}
.search-box {
  background: #ffffff69;
  border-radius: 10px 0 0 10px;
  color: #808080cc;
  border: 2px solid #808080cc;
  width: 50%;
  height: 2rem;
  padding: 1rem;
  font-size: 1.2rem;
  outline: none;
}
.search-box:focus-within {
  color: #000;
  color: initial;
  border-color: #a6b1e1;
  background-color: #fff;
}
.search-box:hover {
  background-color: white;
}
.search-btn {
  border-radius: 0 60px 60px 0;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  height: 2rem;
  padding: 1rem;
  background-color: #a6b1e1;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.25s ease;
  outline: none;
}
.search-btn:hover {
  background-color: white;
  border-color: #a6b1e1;
}
.search-btn:active {
  box-shadow: 1px 1px 5px 0px #808080ab;
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
.location {
  font-size: 3.8rem;
  font-weight: normal;
  color: black;
}
.time {
  font-size: 1.5rem;
  color: black;
}
.current-weather {
  margin: 2rem 0;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.current-weather img {
  width: 8rem;
  object-fit: contain;
  margin: 0 1rem;
  border-radius: 10rem;
  background-color: #886b9f75;
  object-position: 0 -7px;
}
.current-weather p {
  font-size: 1.2rem;
  color: black;
  margin-left: 2rem;
  text-transform: capitalize;
  margin-right: 3rem;
}
.current-weather p span {
  color: #886b9f;
  font-size: 4rem;
}
.last-note {
  position: fixed;
  font-size: 0.75rem;
  right: 0.4rem;
  bottom: 0;
  color: black;
}
.weather-box {
  position: relative;
  width: 50%;
  background-color: white;
  margin: 1rem auto;
  color: black;
  font-size: 1.2rem;
  border-radius: 2rem;
  text-align: left;
  cursor: pointer;
  border: 1px solid grey;
}
.weather-box:hover {
  background-color: rgba(255, 255, 255, 0.37);
}
.weather-box p {
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.down-arrow {
  transition: all 0.3s ease;
}
.down-arrow:hover path {
  stroke: #424874;
}
.rotate {
  -webkit-transform: rotate(180deg) !important;
          transform: rotate(180deg) !important;
}
.rotate path {
  stroke: #424874 !important;
}
ul {
  display: none;
  list-style: none;
  margin: 1rem;
  padding: 0 1rem;
}
.show {
  display: block !important;
}
ul li {
  display: flex;
  align-items: center;
  padding: 0.5rem 0;
}
ul li:hover {
  background-color: rgba(255, 255, 255, 0.534);
}
.weather-time {
  display: inline-block;
  width: 20%;
  text-align: center;
}
.weather-temp {
  display: inline-block;
  width: 20%;
  text-align: center;
}
.weather-desc {
  display: inline-block;
  width: 20%;
  text-align: center;
}
ul li img {
  display: inline-block;
  width: 10%;
  margin: 0 10%;
  text-align: center;
}
.loader {
  border: 0.75rem solid #8f8f8f;
  border-top: 0.75rem solid #3498db;
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}
.loader-container {
  padding: 10rem;
  display: flex;
  justify-content: center;
  align-content: center;
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@media screen and (max-width: 1024px) {
  .search-box,
  .search-btn {
    height: 3rem;
    cursor: default;
  }
  .weather-box {
    cursor: default;
    width: 80%;
  }
  .weather-box:hover {
    background-color: white !important;
  }
  .search-btn:hover {
    background-color: #a6b1e1;
    border-color: transparent;
  }
  .search-btn:active {
    background-color: white;
    border-color: #a6b1e1;
  }
  .down-arrow:hover path {
    stroke: #a6b1e1;
  }
}
@media only screen and (min-width: 550px) and (max-width: 730px) {
  html {
    font-size: 12px;
  }
}
@media only screen and (min-width: 400px) and (max-width: 550px) {
  html {
    font-size: 10px;
  }
  .search-box {
    width: 75%;
  }
}
@media only screen and (max-width: 405px) {
  html {
    font-size: 8px;
  }
  .search-box {
    width: 80%;
  }
  .weather-box {
    width: 95%;
  }
}

